import './index.scss';
import { Collapse } from 'antd';
import { useMemo } from 'react';

const PanelList = () => {
  const { Panel } = Collapse;

  const PanelItemData = useMemo(
    () => [
      {
        title: 'Blockchain Engineer / Backend Developer',
        content: [
          {
            subTitle: 'Job Description',
            text: [
              'We are a FINTECH Company and one of the market leaders for Cryptocurrency and blockchain technology. We are expanding our fast-growing business in Hong Kong and internationally. We are seeking for right candidates to support our latest projects and business features.'
            ]
          },
          {
            subTitle: 'About this role',
            text: [
              {
                css: { marginBottom: '20px' },
                text: 'We are looking for the next blockchain engineer who can take on the responsibilities and accountabilities in developing, designing, and delivering our blockchain based products and features for our fintech business.'
              },
              {
                css: { marginBottom: '10px' },
                text: 'If you have the drive and passion for technology with a strong desire to learn and grow with us. Then you may want to look no further, we are actively on the look-out for our next pool of talents here at our local offices within the CBD:'
              },
              'Degree holder in computer science, information technology, engineering, or related disciplines',
              'At least 3+ years of relevant work experience in web application development',
              'Proficient in bash shell scripting and Linux optimization',
              'Knowledge in Filecoin blockchain, e.g. Lotus wallet, data store and message pool;',
              'Experience building backend systems with one or more programming languages such as Golang FFI, Rust / C or C++, and Linux would be highly desirable',
              'Excellent communication skills Chinese and English'
            ]
          },
          {
            subTitle: 'Preferred Experience',
            text: [
              'Previous knowledge with blockchain technologies and digital assets',
              'Experience in financial systems',
              'If you are interested, parties please submit your CV and details in the form below.'
            ]
          }
        ]
      },
      {
        title: 'Golang Developer / Protocol Engineer',
        content: [
          {
            subTitle: 'Job Description',
            text: [
              'We are a FINTECH Company and one of the market leaders for Cryptocurrency and blockchain technology. We are expanding our fast-growing business in Hong Kong and internationally. We are seeking for right candidates to support our latest projects and business features.'
            ]
          },
          {
            subTitle: 'About this role',
            text: [
              {
                css: { marginBottom: '10px' },
                text: 'We are looking for the next Golang developer | Protocol Engineer. If you have the drive and passion for technology with a strong desire to learn and grow with us. Then you may want to look no further, we are working in an agile environment whereby taking ownership and driving projects into a high note in completion its almost always an absolute must. If you are a techie and able to come in and jump start to fix issues and make improvements across all stack and decks.'
              },
              'Degree holder in computer science, information technology, engineering, or related disciplines',
              'At least 3+ years of relevant work experience in web application development',
              'Knowledge in blockchain, e.g. Lotus wallet, data store and message pool;',
              'Experience building backend systems with one or more programming languages such as Golang FFI, Rust / C or C++ would be highly desirable',
              'Excellent communication skills Cantonese and Mandarin'
            ]
          },
          {
            subTitle: 'Preferred Experience',
            text: [
              'Previous knowledge with blockchain technologies and digital assets',
              'Experience in financial systems',
              'If you are interested, parties please submit your CV and details in the form below.'
            ]
          }
        ]
      },
      {
        title: 'Trading Manager – Digital Assets Platform',
        content: [
          {
            subTitle: 'Job Description',
            text: [
              'We are looking for a Trading Manager to grow a digital assets platform in Hong Kong. We are a FINTECH Company and one of the market leaders for Cryptocurrency and Blockchain, we are now expanding our fast-growing business to Hong Kong, Dubai and Silicon Valley. We are seeking the right candidates to support our expanding business.'
            ]
          },
          {
            subTitle: 'Responsibilities',
            text: [
              'Design, develop and implement Algo trading/quantitative models on the Cryptocurrency market',
              'Develop backtest engine, trading API, responsible for the portfolio of cryptocurrency and implement a trading strategy',
              'Research new market opportunities',
              'Ensure risks are properly managed on a portfolio level',
              'Manage digital asset portfolio positions',
              'Risk Management of trade'
            ]
          },
          {
            subTitle: 'Requirements',
            text: [
              'University graduate with a degree in Computer Science, Programming, Information systems and business and or related disciplines',
              'Cryptocurrency trading strategy experience is preferable',
              '5-8+ years of relevant working experience with trading',
              'Hands-on profitable Algo-trading model is highly preferable',
              'Previous experience in Equity / Future/ FX/ Bond Algo-trading is a plus',
              'Expertise in Python programming skills',
              'Logical in numerical and quantitative analysis skills'
            ]
          },
          {
            subTitle: 'What you be doing with us during the on-boarding stage…',
            text: [
              'Huge responsibilities from Day 1. Be the owner of your own learning curve.',
              'Work in a very dynamic and fast-paced environment. Be part of a growing international team across multiple regions Full involvement in developing brand new products from scratch using the latest technologies alongside a passionate and talented team',
              'If you are interested, parties please submit your CV and details in the form below.'
            ]
          }
        ]
      },
      {
        title: 'Rust Developer',
        content: [
          {
            subTitle: 'Job Highlights',
            text: [
              {
                dot: true,
                css: { marginBottom: '10px' },
                text: 'AR blockchain development experience'
              },
              {
                dot: true,
                css: { marginBottom: '10px' },
                text: 'Good experience with Solidity'
              }
            ]
          },
          {
            subTitle: 'Job Description',
            text: [
              {
                css: { marginBottom: '10px' },
                text: 'Experienced blockchain engineer with a passion for quality code and good design.'
              },
              {
                css: { marginBottom: '10px' },
                text: 'Who not shy away from exploring new languages and tools, acquiring the skills need to get the job done.'
              },
              {
                css: { marginBottom: '10px' },
                text: 'You have opinions about your favorite open-source projects and cryptocurrencies.'
              },
              {
                css: { marginBottom: '10px' },
                text: 'You have experience with Solidity, and you’ve deployed smart contracts in the real world.'
              },
              {
                dot: true,
                css: { marginBottom: '10px' },
                text: 'Real world blockchain development experience'
              },
              {
                dot: true,
                css: { marginBottom: '10px' },
                text: 'Proficient in Rust programming'
              },
              {
                dot: true,
                css: { marginBottom: '10px' },
                text: 'Great written and spoken English and Chinese (either Cantonese and or mandarin)'
              },
              {
                dot: true,
                css: { marginBottom: '10px' },
                text: 'Knowledge about crypto algorithms, e.g. Merkle Tree, ZN-SNARK'
              },
              {
                dot: true,
                css: { marginBottom: '10px' },
                text: 'Utilize and tune hardware performance, e.g. CPU, GPU and Memory'
              },
              {
                dot: true,
                css: { marginBottom: '10px' },
                text: 'Experience working with an asynchronous remote team'
              },
              {
                dot: true,
                css: { marginBottom: '10px' },
                text: 'You know how to write a pull-request and perform a code review'
              },
              {
                dot: true,
                css: { marginBottom: '10px' },
                text: 'Understanding of security issues and best practices'
              }
            ]
          },
          {
            subTitle: 'Benefit',
            text: [
              {
                dot: true,
                css: { marginBottom: '10px' },
                text: 'Unlimited Coffee, teas, and soft drink'
              },
              {
                dot: true,
                css: { marginBottom: '10px' },
                text: 'Daily Complimentary Fruits'
              },
              {
                dot: true,
                css: { marginBottom: '10px' },
                text: 'Flat hierarchy'
              },
              {
                dot: true,
                css: { marginBottom: '10px' },
                text: 'Periodical team zoom meeting’s around the regions ( COVID permitting)'
              },
              {
                css: { marginTop: '15px' },
                text: 'If you are interested, parties please submit your CV and details in the form below.'
              }
            ]
          }
        ],
        topP: 'We are a FINTECH Company and one of the market leaders for Cryptocurrency and blockchain technology. We are expanding our fast-growing business in Hong Kong and internationally. We are seeking for right candidates to support our latest projects and business features.'
      },
      {
        title: 'Flutter Developer (Web | Android & IOS)',
        content: [
          {
            subTitle: 'Job Highlights',
            text: [
              {
                dot: true,
                css: { marginBottom: '10px' },
                text: 'Mobile App development'
              },
              {
                dot: true,
                css: { marginBottom: '10px' },
                text: 'Flutter and Android'
              }
            ]
          },
          {
            subTitle: 'Responsibilities:',
            text: [
              {
                dot: true,
                css: { marginBottom: '10px' },
                text: 'Responsible for development of mobile apps in Flutter + Android'
              },
              {
                dot: true,
                css: { marginBottom: '10px' },
                text: 'Contribute on user journey and logic flows'
              }
            ]
          },
          {
            subTitle: 'Requirements:',
            text: [
              {
                dot: true,
                css: { marginBottom: '10px' },
                text: 'Higher diploma or above in Computer Science, Programming or equivalent'
              },
              {
                dot: true,
                css: { marginBottom: '10px' },
                text: 'Solid experience in mobile app development in Flutter is highly preferable'
              },
              {
                dot: true,
                css: { marginBottom: '10px' },
                text: 'Hands-on in using ios and or Android'
              },
              {
                dot: true,
                css: { marginBottom: '10px' },
                text: 'Responsible, self-motivated and independents'
              },
              {
                dot: true,
                css: { marginBottom: '10px' },
                text: 'Excellent team player'
              },
              {
                dot: true,
                css: { marginBottom: '10px' },
                text: 'Time management skills'
              },
              {
                dot: true,
                css: { marginBottom: '25px' },
                text: '5-day work week'
              },
              'If you are interested, parties please submit your CV and details in the form below.'
            ]
          }
        ],
        topP: 'We are a FINTECH Company and one of the market leaders for Cryptocurrency and blockchain technology. We are expanding our fast-growing business in Hong Kong and internationally. We are seeking for right candidates to support our latest projects and business features.'
      },
      {
        title: 'Product Manager – Blockchain',
        content: [
          {
            subTitle: 'Job Highlights',
            text: [
              {
                dot: true,
                css: { marginBottom: '10px' },
                text: 'Fintech'
              },
              {
                dot: true,
                css: { marginBottom: '10px' },
                text: 'Product management'
              },
              {
                dot: true,
                css: { marginBottom: '10px' },
                text: 'Blockchain'
              }
            ]
          },
          {
            subTitle: 'Job Description',
            text: [
              {
                css: { marginBottom: '10px' },
                text: 'Product Manager job to drive product initiatives from inception through execution. Your new company'
              },
              {
                css: { marginBottom: '10px' },
                text: 'We are a FINTECH Company and one of the market leaders for Cryptocurrency and blockchain technology. We are expanding our fast-growing business in Hong Kong and internationally. We are seeking for right candidates to support our latest projects and business features. Your new role'
              },
              {
                dot: true,
                css: { marginBottom: '10px' },
                text: "Create and maintain the team's roadmap, milestones, and dependencies with strategic priorities in mind"
              },
              {
                dot: true,
                css: { marginBottom: '10px' },
                text: 'Define features, write product specifications, facilitate user studies, iterate on feedback, help write test plans with Trading Managers and Developers, manage the backlog, and ultimately ship products/features in a timely manner'
              },
              {
                dot: true,
                css: { marginBottom: '10px' },
                text: 'Interview and interact with customers to build empathy, ensuring that their needs are met'
              },
              {
                dot: true,
                css: { marginBottom: '10px' },
                text: 'Prioritize initiatives and projects appropriately and weigh tradeoffs that must be made'
              },
              {
                dot: true,
                css: { marginBottom: '10px' },
                text: 'Perform market research and analysis'
              }
            ]
          },
          {
            subTitle: "What you'll need to succeed",
            text: [
              {
                dot: true,
                css: { marginBottom: '10px' },
                text: '4+ years of product/project management experience or equivalent'
              },
              {
                dot: true,
                css: { marginBottom: '10px' },
                text: 'Experience shipping and iterating on successful Software or Financial products at scale'
              },
              {
                dot: true,
                css: { marginBottom: '10px' },
                text: 'Product management experience on consumer product onboarding is highly preferable'
              },
              {
                dot: true,
                css: { marginBottom: '10px' },
                text: 'Demonstrated leadership with a focus on getting things done autonomously'
              },
              {
                dot: true,
                css: { marginBottom: '10px' },
                text: 'The ability to collaborate well with designers and engineers'
              },
              {
                dot: true,
                css: { marginBottom: '10px' },
                text: 'An analytical and data-driven work style'
              },
              {
                dot: true,
                css: { marginBottom: '40px' },
                text: 'Excellent interpersonal, communication, organization, and prioritization skills'
              },
              'If you are interested, parties please submit your CV and details in the form below.'
            ]
          }
        ]
      },
      {
        title: 'Data Center Engineer',
        content: [
          {
            subTitle: 'Company Overview',
            text: [
              'Coffee Cloud gives your technology the caffeine kick it needs. Our solution is designed to be Customizable, Secure and Scalable. We are the perfect partner for Cloud & Data Solutions. Based in the heart of Asia, in the Greater Bay Area with a Global reach. Our international clients and partners trust us to provide exception quality digital and data solutions.'
            ]
          },
          {
            subTitle: 'Responsibilities:',
            text: [
              {
                dot: true,
                text: 'Facilities Engineer: Carry out preventive and corrective maintenance to all facilities in the data center. Routine inspections. Liaise with and manage works of contractors'
              },
              {
                dot: true,
                text: 'DC Engineer: Assist in tendering, project planning, site supervision, testing & commissioning'
              }
            ]
          },
          {
            subTitle: 'Requirements',
            text: [
              {
                dot: true,
                text: 'Tertiary educated in Programming, Computer Engineering and or related discipline'
              },
              {
                dot: true,
                text: 'Min. 3 years system installation / maintenance experience for data center projects'
              },
              {
                dot: true,
                text: 'Familiar with UPS, CRAC, chiller, switchboards'
              },
              {
                dot: true,
                text: 'Registered Electrical Worker, REW A(0) or above'
              },
              {
                dot: true,
                text: 'Good communication skills'
              },
              {
                dot: true,
                css: { marginBottom: '20px' },
                text: 'Able to adapt in the command of languages'
              },
              'If you are interested, parties please submit your CV and details in the form below.'
            ]
          }
        ]
      }
    ],
    []
  );

  return (
    <>
      <Collapse
        accordion
        expandIconPosition={'right'}
        defaultActiveKey={['0']}
        ghost
      >
        {PanelItemData.map((item, index) => (
          <Panel header={item.title} key={index}>
            <div key={index}>
              {item?.topP ? <p key={index}>{item?.topP}</p> : <></>}
              {item?.content.map((item, index) => (
                <>
                  <div className="panel_title" key={index}>
                    {item.subTitle}
                  </div>
                  {item.text.map((item, index) => (
                    <p key={index} style={item?.css || null}>
                      {item?.dot ? (
                        <span style={{ fontWeight: 'bolder' }}>
                          &nbsp;·&nbsp;
                        </span>
                      ) : null}
                      {item.text || item}
                    </p>
                  ))}
                </>
              ))}
            </div>
          </Panel>
        ))}
      </Collapse>
    </>
  );
};

export default PanelList;
